import React, { Component } from 'react';
import cx from 'classnames';
import styles from './styles.scss';
import AWS from 'aws-sdk';

import jshue from 'jshue';
const hue = jshue();

import listen from '../Helpers/Listen';

const lex = new AWS.LexRuntime({
  region: 'us-east-1',
  secretAccessKey: 'iqbPa4ZiiT0Wu+KGdvXfvz2bZTeySac1EEU7iKH7',
  accessKeyId: 'AKIAJ2CUCDT5I6LLXU7Q',
});

export default class LexExample extends Component{
  constructor(props) {
    super(props);
    this.state = {
      recording: listen.isListening
    };
    listen.addListener((speechText) => {
      console.log('event emitted',speechText);
      lex.postContent({
          botAlias: 'UsfulTestBot',
          botName: 'UsfulLexBot',
          contentType: 'text/plain; charset=utf-8',
          inputStream: speechText,
          userId: 'dev00',
        }, (err, response) => {
          const slots = response.slots;
          console.log(slots);
          this.setState({
            recording: false
          });
        }
      );
    });
    this.hueBridge = hue.bridge('192.168.0.10');
    console.log(this.hueBridge);
  }

  render() {
    return (
      <div>
        <h1>
          Amazon Lex Example
        </h1>
        <div>
          <h2 style={{color: this.hueBridge ? 'green' : 'red'}}>{this.hueBridge ? 'Hue Bridge Not Connected' : 'Hue Bridge Connected'}</h2>
          {
            this.state.recording ? <button onClick = {() => {
              listen.stop();
              this.setState({
                recording: false
              })
            }}>Stop recording</button>
              : <button
                onClick = {() => {
                  listen.getResponse();
                  this.setState({
                    recording: true
                  });
                }}
                disabled = {!this.hueBridge}
                > Start recording </button>
          }
        </div>
      </div>
    );
  }
}



